import PageLayout from '../components/PageLayout';
import Utils from '../util/Utils';

import '../styles/grid.scss';

class Grid extends React.Component {
    colCount = Math.ceil(Math.sqrt(names.length));
    rowCount = Math.ceil(names.length / rowCount);
    state = {
        highlight: null,
        names: Utils.getNames(),
    };
    componentDidMount() {
        Utils.loadVoices();
        window.setInterval(() => {
            const highlight = Math.floor(Math.random() * this.state.names.length);
            this.setState({ highlight });
        }, 100);
    }
    render() {
        const { names } = this.state;

        const style = {
            width: 100 / colCount + '%',
            height: 100 / rowCount + '%'
        };

        return (
            <PageLayout className="grid">
                <div className="page_content">
                    {names.map((n, i) =>
                        <div className="name-wrapper" key={n.value} style={style}>
                            <div className="name" data-highlight={i === this.state.highlight}>{n.value}</div>
                        </div>
                    )}
                </div>
            </PageLayout>
        );
    }
}

export default Grid;